import React, { useEffect, useState } from 'react';
import Head from 'next/head';
import '../styles/home.css'; // Assuming this file contains your old styles
import Navbar from '../components/Navbar';
import LoginModal from '../components/LoginModal';
import SignupModal from '../components/SignupModal';
import AddMatchModal from '../components/AddMatchModal';
import Table from '../components/Table';
import { useAuth } from '../context/AuthContext';

const Home = () => {
  const [matches, setMatches] = useState([]);
  const [showLoginModal, setShowLoginModal] = useState(false);
  const [showSignupModal, setShowSignupModal] = useState(false);
  const [showAddMatchModal, setShowAddMatchModal] = useState(false);
  const { currentUser, logout, isLoginModalOpen, setIsLoginModalOpen } = useAuth();

  useEffect(() => {
    fetchMatches();
  }, []);

  const fetchMatches = () => {
    fetch('/api/matches')
      .then(response => response.json())
      .then(data => setMatches(data))
      .catch(error => console.error('Error fetching matches:', error));
  };

  const deleteMatch = (id) => {
    fetch(`/api/matches/${id}`, {
      method: 'DELETE',
      headers: { 'Content-Type': 'application/json' },
    })
      .then(response => {
        if (response.ok) {
          fetchMatches();
        } else {
          console.error('Failed to delete match');
        }
      })
      .catch(error => console.error('Error during delete request:', error));
  };

  const sortTable = (column, order) => {
    const sortedMatches = [...matches].sort((a, b) => {
      let compareA = a[column];
      let compareB = b[column];

      if (column === 'match_date') {
        compareA = new Date(a[column]);
        compareB = new Date(b[column]);
      }

      if (compareA < compareB) {
        return order === 'asc' ? -1 : 1;
      } else if (compareA > compareB) {
        return order === 'asc' ? 1 : -1;
      } else {
        return 0;
      }
    });
    setMatches(sortedMatches);
  };

  return (
    <>
      <Head>
        <meta charSet="UTF-8" />
        <meta name="viewport" content="width=device-width, initial-scale=1, viewport-fit=cover" />
        <title>Football Matches</title>
      </Head>
      <div className="bg-light">
        <Navbar currentUser={currentUser} logout={logout} />
        <div className="container mt-4">
          <h1>Matches</h1>
          <Table data={matches} sortTable={sortTable} deleteMatch={deleteMatch} />
          <button onClick={() => setShowAddMatchModal(true)} className="btn btn-primary mt-2">Add Match</button>
        </div>
      </div>
      {showLoginModal && <LoginModal onClose={() => setShowLoginModal(false)} showSignupModal={() => { setShowLoginModal(false); setShowSignupModal(true); }} />}
      {showSignupModal && <SignupModal onClose={() => setShowSignupModal(false)} />}
      {showAddMatchModal && <AddMatchModal onClose={() => setShowAddMatchModal(false)} />}
    </>
  );
};

export default Home;