import React, { useState } from 'react';

const LoginModal = ({ onClose, showSignupModal }) => {
  const [formData, setFormData] = useState({
    username: '',
    password: ''
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    // Handle form submission logic here
    console.log('Log In Data:', formData);
    onClose();
  };

  return (
    <div className="modal fade show" id="login-modal" tabIndex="-1" aria-labelledby="login-modal-label" aria-hidden="true" style={{ display: 'block' }}>
      <div className="modal-dialog">
        <div className="modal-content">
          <div className="modal-header">
            <h5 className="modal-title" id="login-modal-label">Log In</h5>
            <button type="button" className="btn-close" aria-label="Close" onClick={onClose}></button>
          </div>
          <div className="modal-body">
            <form id="login-form" onSubmit={handleSubmit}>
              <label htmlFor="login-username">Username</label>
              <input type="text" id="login-username" name="username" required className="form-control" value={formData.username} onChange={handleChange} />
              
              <label htmlFor="login-password">Password</label>
              <input type="password" id="login-password" name="password" required className="form-control" value={formData.password} onChange={handleChange} />
              
              <button type="submit" id="login-submit" className="btn btn-primary mt-2">Log In</button>
            </form>
            <div className="mt-3 text-center">
              <a href="#" id="show-signup-link" className="text-decoration-none" onClick={showSignupModal}>Don't have an account? <strong>Sign up now!</strong></a>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default LoginModal;